import React from 'react';
import { Slide } from 'react-slideshow-image';
import 'react-slideshow-image/dist/styles.css';
import './Slideshow.css'

const Slidess = () => {
    const images = [
        "https://media.licdn.com/dms/image/D5622AQFUSVTk2OhZ2w/feedshare-shrink_2048_1536/0/1689582420630?e=1704326400&v=beta&t=tQbSsjK1jDVZufeed3jC2Pu9KEB55vv2jIGqZn_0uKM",
        "https://media.licdn.com/dms/image/C5622AQHabJJbv97PEA/feedshare-shrink_2048_1536/0/1661746727385?e=1704326400&v=beta&t=hrQtPEh18HjYUcwJCUhLE41zv3gsYmIdApIfSikkLzQ",
        "https://media.licdn.com/dms/image/C5622AQHTGIkxps8HPA/feedshare-shrink_2048_1536/0/1669652344608?e=1704326400&v=beta&t=LU7q1rWEulvaCKBmXVEEVbLy2I1awMp9Xj6RXxjI414",
    ];

    return (
        <div className='slide-container'>
        <Slide>
            
            <div className="each-slide-effect ">
                <div style={{ 'backgroundImage': `url(${images[0]})` }}>
                    <span>Slide 1</span>
                </div>
            </div>
            <div className="each-slide-effect">
                <div style={{ 'backgroundImage': `url(${images[1]})`}}>
                    <span>Slide 2</span>
                </div>
            </div>
            <div className="each-slide-effect">
                <div style={{ 'backgroundImage': `url(${images[2]})` }}>
                    <span>Slide 3</span>
                </div>
            </div>
        </Slide>
        </div>
    );
};

export default Slidess;